@import-normalize;

body {
    font-family: Roboto, Cantarell, sans-serif;
}

img {
    display: block;
    max-height: 100%;
    height: auto;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}